/** @jsx jsx */
import { jsx } from 'theme-ui'
import React from 'react'
import { Box, Flex } from 'rebass'
import images from 'src/images'
import styles from './styles'

const Header = (props) => {
  const listIcon = [
    {
      url: 'iconTelegram'
    },
    {
      url: 'iconTwitter'
    },
    {
      url: 'iconDiscord'
    },
    {
      url: 'iconMedium'
    },
    {
      url: 'iconGithub'
    }
  ]

  const renderListIcon = () => {
    return listIcon.map((item, i) => {
      return (
        <Box mr="0" key={i}>
          <img sx={styles.imgIcon} src={images[item.url]} />
        </Box>
      )
    })
  }
  return (
    <>
      <Flex sx={styles.headerContainer} width="100%" justifyContent="flex-end">
        <Box>
          <img sx={styles.headerBg} src={images.headerLogo} />
        </Box>
        <Flex mr="28px">{renderListIcon()}</Flex>
      </Flex>

      <Flex justifyContent="space-between">
        <img sx={styles.logoRespon} src={images.hakkaLogo} alt="" />
        <img sx={styles.iconMenu} src={images.iconMenu} alt="" />
      </Flex>
      {/* </Box> */}
    </>
  )
}
export default Header
