export default {
  headerBg: {
    position: 'absolute',
    width: '100%',
    top: '0px',
    right: '0px',
    zIndex: '-1'
  },

  headerContainer: {
    position: 'relative',
    padding: '12px 0',
    '@media screen and (max-width: 576px)': {
      display: 'none'
    }
    // position: "relative",
    // top: "-253px",
    // right: 0,
  },

  logoRespon: {
    display: 'none',

    '@media screen and (max-width: 576px)': {
      display: 'block'
    }
  },

  iconMenu: {
    display: 'none',

    '@media screen and (max-width: 576px)': {
      display: 'block'
    }
  },

  imgIcon: {
    cursor: 'pointer',
    transition: '.3s all',
    opacity: '.5',

    ':hover': {
      opacity: 1
    }
  }
}
