/** @jsx jsx */
import { jsx } from 'theme-ui'
import React from 'react'
import { Box, Flex, Heading } from 'rebass'
import styles from './styles'
import images from 'src/images'
import CoinComponent from 'src/components/Common/CoinComponent'

function TokenMetrics (props) {
  return (
    <>

      <Box sx={styles.tokenMetrics_responsive}>

        <Box sx={styles.tokenMetricsChain}>{props.renderChain()}</Box>

        <Box sx={styles.tokenMetricsInfoChain} ml="2">
          <Box sx={styles.tokenMetricsSubHead}>Token Metrics</Box>

          <Box sx={styles.tokenMetricsInfoContainer} mt="20px">
            <Box sx={styles.tokenMetricsInfo}>
              NAME: <span sx={styles.info}>Hakka Finance (HAKKA)</span>
            </Box>
            <Box sx={styles.tokenMetricsInfo} mt="2">
              TYPE: <span sx={styles.info}>ERC-20 (Ethereum-based token)</span>
            </Box>
            <Box sx={styles.tokenMetricsInfo} mt="2">
              CONTRACT ADDRESS: <br />{' '}
              <Flex sx={styles.tokenMetricsInfoAddress} alignItems="center">
                <span sx={styles.info_link}>
                  0x0E29e5AbbB5FD88e28b2d355774e73BD47dE3bcd
                </span>{' '}
                <img sx={styles.iconScan} src={images.iconEtherscan} alt="" />
              </Flex>
            </Box>
          </Box>

          <Box sx={styles.tokenMetricsChainList} mt="20px">
            Get HAKKA Token on Ethereum:
          </Box>
          <Flex sx={styles.token_metric_responsive} mt="2">
              {/* {props.renderChainToken()} */}
                <Box>
                  <CoinComponent whatHakka={true} item={{ imageCoin: 'iconInch', coinName: '1inch' }} />
                </Box>
                <Flex >
                  <Box className="left-bot-token">
                    <CoinComponent whatHakka={true} item={{ imageCoin: 'iconUniswap', coinName: 'Uniswap' }} />
                  </Box>
                  <Box className="right-bot-token" >
                    <CoinComponent whatHakka={true} item={{ imageCoin: 'iconBalancer', coinName: 'Balancer' }} />
                  </Box>
                </Flex>
            </Flex>
        </Box>

      </Box>
    </>
  )
}

export default TokenMetrics
