export default {
  // token metrics

  tokenMetrics_responsive: {
    display: 'grid',
    gridGap: '0px',
    gridTemplateColumns: '1fr 1fr',
    maxWidth: '1200px',
    '@media screen and (max-width: 960px)': {
      gridTemplateColumns: '1fr'

    }
  },
  tokenMetricsChain: {
    // width: '30%',
    padding: '16px 12px',
    paddingLeft: 0,
    fontSize: [2, 3, 4, 5],
    flex: '0.8',

    '@media screen and (max-width: 576px)': {
      padding: '0px',
      width: '100%',
      display: 'flex',
      justifyContent: 'space-between'
    }
  },

  tokenMetricsInfoChain: {
    // width: "30%",
    // marginLeft: '105px',
    borderTop: '1px solid #2da287',
    paddingTop: '27px',
    flex: '1',

    '@media screen and (max-width: 576px)': {
      marginLeft: 0,
      borderTop: 'unset'
    }
  },

  tokenMetricsInfoContainer: {
    marginBottom: '20px'
  },

  tokenMetricsSubHead: {
    fontSize: [1, 2, 3, 4],
    color: '#253e47',
    fontWeight: 'bold'
    // marginBottom: '20px'
  },

  iconScan: {
    width: '24px'
  },

  tokenMetricsInfo: {
    fontSize: [0, 1, 2, 3],
    fontWeight: 600,
    color: '#253e47'
  },

  tokenMetricsInfoAddress: {
    cursor: 'pointer',

    '& iconScan': {
      opacity: '.5'
    },

    ':hover': {
      '& iconScan': {
        opacity: 1
      }
    }
  },

  info: {
    fontWeight: 'normal'
  },
  info_link: {
    fontWeight: 'normal',
    textDecoration: 'underline'
  },

  tokenMetricsChainList: {
    fontSize: [0, 1, 2, 3],
    fontWeight: 600,
    color: '#253e47',
    paddingBottom: [1, 3, 3, 3]
  },
  token_metric_responsive: {
    '@media screen and (max-width: 1180px)': {
      flexDirection: 'column',
      '& .left-bot-token': {
        width: '50%',
        '@media screen and (max-width: 576px)': {
          paddingRight: '12px'
        }
      },
      '& .right-bot-token': {
        width: '50%'
      }
    }
  }
}
