export default {
  footerContainer: {
    paddingLeft: '12.8%',
    // overflow: 'auto',
    // maxHeight: '200px',
    '@media screen and (max-width: 576px)': {
      padding: '0 16px'
    }
  },

  footerContent: {
    borderTop: '1px solid #dae1e3',
    paddingTop: '27px'
    // marginBottom: '72px'
  },

  logoFooter: {
    '@media screen and (max-width: 576px)': {
      display: 'none'
    }
  },

  textCopy: {
    fontSize: [0, 1, 2],
    color: 'rgba(37, 62, 71, 0.5)',
    fontWeight: 600,
    paddingBottom: '28px',

    '@media screen and (max-width: 576px)': {
      margin: '0 auto',
      textAlign: 'center'
    }
  },

  imgIcon: {
    cursor: 'pointer',
    transition: '.3s all',
    opacity: '.5',

    ':hover': {
      opacity: 1
    }
  }
}
