export default {
  homescreenContainer: {
    // position: "relative",
    // top: "-260px",
    paddingLeft: '12.8%',
    paddingRight: '15%',

    '@media screen and (max-width: 576px)': {
      paddingLeft: '5%',
      paddingRight: '5%'
    }
  },

  homescreenSubTitle: {
    color: '#2da287',
    fontSize: [0, 1, 2, 3],
    fontWeight: '600'
  },

  spanLink: {
    textDecoration: 'underline',
    paddingLeft: '3px',
    fontSize: [0, 1, 2, 3]
  },

  homeHeading: {
    fontSize: [6, 7, 8, 8],
    color: '#0b555a',
    width: '100%',
    fontWeight: '300',
    paddingTop: '36px',
    paddingBottom: ['36px', '64px', '64px', '64px']
  },

  homeDescription: {
    fontSize: [1, 2, 3, 4],
    color: 'black',
    maxWidth: '1200px'

  },

  // Card top
  homeCardTop: {
    paddingBottom: ['100px', '160px', '160px', '160px']
  },

  homeTotalValue: {
    padding: '0 12.8%',
    paddingRight: 0,
    paddingBottom: ['100px', '176px', '176px', '176px'],

    '@media screen and (max-width: 576px)': {
      paddingLeft: '5%'
      // paddingRight: '5%'
    }
  },

  // what hakka
  whatHakka: {
    paddingBottom: ['48px', '160px', '160px', '160px']
    // '@media screen and (max-width: 576px)': {
    //   paddingBottom: '160px',
    // }
  },

  // token metrics
  tokenMetrics: {
    paddingBottom: ['48px', '160px', '160px', '160px'],
    width: '100%',

    '@media screen and (max-width: 960px)': {
      flexDirection: 'column',
      paddingLeft: '0px'
    }
  },

  chainContent_active: {
    maxWidth: '290px',
    cursor: 'pointer',
    '@media screen and (max-width: 576px)': {
      flexDirection: 'column',
      padding: '16px',
      alignItems: 'center',
      borderTop: '1px solid #2da287',
      '& .chain-left-content': {
        flexDirection: 'column'
      },
      '& .chain-text': {
        paddingLeft: '0px'
      },
      '& .icon-right': {
        opacity: '0',
        transition: 'all ease 0.2s'
      }
    }
  },
  chainContent: {
    maxWidth: '290px',
    cursor: 'pointer',
    '@media screen and (max-width: 576px)': {
      flexDirection: 'column',
      padding: '16px',
      alignItems: 'center',
      borderTop: '1px solid #ebf0f2',
      '& .chain-left-content': {
        flexDirection: 'column'
      },
      '& .chain-text': {
        paddingLeft: '0px'
      },
      '& .icon-right': {
        opacity: '0',
        transition: 'all ease 0.2s'
      }
    }
  },

  imgChain: {
    width: '40px',
    height: '40px',
    padding: '8px',
    backgroundColor: '#f2f2f2',
    borderRadius: '8px',

    '@media screen and (max-width: 576px)': {
      paddingBottom: '12px'
    }
  },
  imgChain_gray: {
    width: '40px',
    height: '40px',
    padding: '8px',
    backgroundColor: '#f2f2f2',
    borderRadius: '8px',
    transition: 'all ease 0.5s',
    filter: 'grayscale(100%)',
    opacity: '.5',

    '@media screen and (max-width: 576px)': {
      paddingBottom: '12px'
    }
  },
  width_big: {
    width: '500px'
  },

  chainName_active: {
    fontSize: [1, 3, 4, 5],
    color: '#253e47',
    fontWeight: 600,
    paddingLeft: '12px'

  },
  chainName: {
    fontSize: [1, 3, 4, 5],
    color: 'text_grey',
    fontWeight: 600,
    paddingLeft: '12px'
  },

  iconRight: {
    display: 'flex',
    alignItems: 'center',

    '@media screen and (max-width: 576px)': {
      display: 'none'
    }
  },

  // utility hakka
  utilityHakka: {
    paddingBottom: ['48px', '160px', '160px', '160px']
  },

  // partners and intergrator
  partners: {
    paddingBottom: ['48px', '160px', '160px', '160px']
  },

  partnersHead: {
    fontSize: [5, 6, 7, 7],
    color: '#253e47',
    fontWeight: 'normal',
    paddingBottom: '36px'
  },
  img_partners_responsive: {
    '@media screen and (max-width: 1080px)': {
      justifyContent: 'center'
    }
  },
  imgPartner: {
    // width: '20%',
    paddingRight: '28px',
    objectFit: 'contain'

    // '@media screen and (max-width: 576px)': {
    //   width: '45%'
    // },

    // ':nth-child(2)': {
    //   '@media screen and (max-width: 576px)': {
    //     paddingRight: 0
    //   }
    // },

    // ':last-child': {
    //   '@media screen and (max-width: 576px)': {
    //     width: '80%',
    //     padding: '12px auto 0'
    //   }
    // }
  }
}
