export default {
  totalValueContainer: {
    // paddingLeft: "20px",
    position: 'relative'
  },

  totalValueHeadNum: {
    // marginBottom: '36px'
  },

  totalValueHead: {
    fontSize: [2, 3, 4, 5],
    color: '#253e47',
    // marginBottom: '12px',
    fontWeight: 'bold',

    '@media screen and (max-width: 768px)': {
      whiteSpace: 'nowrap'
    }
  },

  totalValueMoney: {
    fontSize: [5, 6, 7, 7],
    color: '#3ebd93',
    fontWeight: '600',

    '@media screen and (max-width: 768px)': {
      whiteSpace: 'nowrap'
    }
  },

  totalValueSubText: {
    fontSize: [0, 1, 2, 3],
    color: 'text_grey',
    // marginBottom: '20px',
    fontWeight: '600'
  },

  listProducts: {
    '@media screen and (max-width: 768px)': {
      flexDirection: 'column',
      display: 'block',
      justifyContent: 'flex-start'
    }
  },

  totalValueImg: {
    backgroundColor: 'transparent',
    position: 'absolute',
    right: 0,
    zIndex: '-1',
    width: '25vw',

    '@media screen and (max-width: 768px)': {
      width: '200px',
      height: '151px',
      zIndex: '-1',
      position: 'relative',
      top: '-175px'
    }
  }
}
